.panel-overlay{
}
.project-slide-wrapper {
    max-height: 45dvh;
    height: 45dvh;
    
    display: flex;
    flex-direction: row;  
    align-items: stretch;
    justify-content: space-between;
    border-radius: 1rem;
    // background-color: $theme-dark-panel-bg;
    // border: solid 2px $theme-light-overlay;
    // background-color: greenyellow;
    padding: 2.5%;
    gap: 2%;
    .project-slide-detail-col {
        // background-color: deeppink;
        width: 60%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        .featured-project-header{

            .featured-project-name {
                @include font-heading-2;
            }
            .featured-project-subtitle {
                @include paragraph-lg;
                line-height: 1.5em;
                margin: 0;
            }
        }
        .featured-project-body{
            
            .featured-project-paragraph {
                @include paragraph-md;
            }
        }

        .featured-project-footer{
            
            .featured-project-role {
                @include paragraph-sm;
            }
            .featured-project-tecstack {}
        }
        

    }
    .project-slide-image-col {
        // background-color: gold;
        width: 42%;
        .featured-project-img-wrapper {
            // background-color: red;
            width: 100%;
            height: 100%;
            max-width: 100%;
            max-height: 100%;
            display: flex;
            justify-content: flex-end;
            .featured-project-img {
                width: 100%;
                height: 100%;
                object-fit: contain;
                // display: none;
            }
        }
        
    }
}
