}

.prev-arrow{
    display: none;
}

.next-arrow{
    display: none;
}


@media screen and (max-width: 768px) {
    
    .project-slide-wrapper {
        max-height: 58rem;
        height: 58rem;
        display: flex;
        flex-direction: column-reverse;
        align-items: center;
        justify-content: center;
        //background-color: yellowgreen;
        
        .project-slide-detail-col {
            width: 100%;
        }

        .project-slide-image-col {
            //background-color: gold;
            width: 100%;
            aspect-ratio: 3 / 2;
            //padding: 2%;
            display: flex;
            justify-content: center;
            align-items: center;
            height: 50%;

            .featured-project-img-wrapper {
                // background-color: red;
                width: 100%;
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                
                .featured-project-img {
                    max-width: 100%;
                    max-height: 100%;
                    object-fit:scale-down;
                }
            }
        }
    }
}








